<template>
    <div class="nav-main">
        <b-navbar>
            <b-navbar-nav>
                <b-nav-item-dropdown text="" id="menu-id-intro">
                    <b-dropdown-item to="/intro/brief" @click.native="addSubMenu($event)"></b-dropdown-item>
                    <b-dropdown-item to="/intro/loc" @click.native="addSubMenu($event)"></b-dropdown-item>
                    <b-dropdown-item to="/intro/adoption" @click.native="addSubMenu($event)"></b-dropdown-item>
                </b-nav-item-dropdown>

                <b-nav-item-dropdown text="" id="menu-id-iacuc">
                    <b-dropdown-item to="/iacuc/regulations" @click.native="addSubMenu($event)"></b-dropdown-item>
                    <b-dropdown-item to="/iacuc/member" @click.native="addSubMenu($event)"></b-dropdown-item>
                    <b-dropdown-item to="/iacuc/review" @click.native="addSubMenu($event)"></b-dropdown-item>
                </b-nav-item-dropdown>

                <b-nav-item-dropdown text="" id="menu-id-training">
                    <b-dropdown-item to="/training/announcement" @click.native="addSubMenu($event)"></b-dropdown-item>
                    <b-dropdown-item to="/training/signup" @click.native="addSubMenu($event)"></b-dropdown-item>
                    <b-dropdown-item to="/training/passlist" @click.native="addSubMenu($event)"></b-dropdown-item>
                </b-nav-item-dropdown>

                <b-nav-item to="/bulletin" @click.prevent="resetSubMenu('')"></b-nav-item>

                <b-nav-item-dropdown text="" id="menu-id-download">
                    <b-dropdown-item to="/download/applyform" @click.native="addSubMenu($event)"></b-dropdown-item>
                    <b-dropdown-item to="/download/guide" @click.native="addSubMenu($event)"></b-dropdown-item>
                    <b-dropdown-item to="/download/specs" @click.native="addSubMenu($event)"></b-dropdown-item>
                </b-nav-item-dropdown>

                <b-nav-item to="/links" @click.prevent="resetSubMenu('')"></b-nav-item>
            </b-navbar-nav>
        </b-navbar>
    </div>
</template>

<script>
export default {
  data() {
      return {
          hover: true
      }
  },
  methods: {
      addSubMenu(e) {
        var menuName = e.target.parentElement.parentElement.parentElement.id;
        var subMenuLinks = document.getElementById(menuName).children[1].cloneNode(true);
        var subMenu = document.getElementById('sub-menu');
        Array.prototype.forEach.call(subMenuLinks.children, child => {
            child.children[0].classList.remove("dropdown-item");
        });
        subMenu.innerHTML = subMenuLinks.innerHTML
      },
      resetSubMenu(name) {
          var subMenu = document.getElementById('sub-menu');
          subMenu.innerHTML = "<p>"+name+"</p>";
      }
  }
}
</script>

<style scoped>
.nav-main {
    width: 100%;
    display: grid;
    justify-content: center;
}
</style>

